#LoginScreen{
    
    div.btn-holders {
        position: fixed;
        width: 100%;
        bottom: 0px;
        a.btn-social-logins {
            color: white !important;
            margin: 0px auto;
            width: 90% !important;
            margin-bottom: 10px;
            height: 50px;
            line-height: 35px;
            font-size: 20px;
            padding-left: 64px !important;
        }
        a.btn-social-logins>:first-child {
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            width: 50px;
            line-height: 46px;
            font-size: 1.6em;
            text-align: center;
            border-right: 1px solid rgba(0,0,0,0.2);
        }
    }

    .btn-google {
        background-color: #dd4b39;
        color: white
    }

    .logo-container {
		margin: 0px auto;
		margin-top: 10rem;

		.logo {
			width: 100%;
			height: 10rem;
			margin: 0 auto;
			background: url(../img/logo.png) no-repeat center center;
            background-size: contain;
            margin-left: 25px !important;
		}
    }
    .switch-title{
        text-align: center;
        color: white;
        font-weight: 200;
        font-size: 22px;
        margin-top: 10px;
    }
    .button{
        color: white !important;
        margin-left: 5%;
        width: 90% !important;
    }
    div.page--full_login__background {
        @media only screen and (max-width: 1920px) {
        background: url("../img/login/1920_1280.png") no-repeat !important;
        background-size: initial;
        }
    }
    
    div.page--full_login__background {
        @media only screen and (max-width: 1600px) {
